import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { ColEditDelete, CartHeadAction, TableData } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import routeConstant from 'routes/route/routeConstant';
import { getTestSessions, deleteTestSession } from 'services/testSession';

const TestSession = () => {
  const [reload, setReload] = useState(0);

  const handleDelete = async id => {
    try {
      await deleteTestSession(id);
      message.success('Xóa thành công');
      setReload(reload + 1);
    } catch (err) {
      console.log(err);
    }
  };

  const columns = [
    {
      title: 'Tên ca thi',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <ColEditDelete
          linkEdit={{
            to: {
              pathname: routeConstant.DANH_MUC__CA_THI__NHAP_DU_LIEU,
              search: `?id=${record.id}`,
            },
          }}
          onConfirmDelete={() => handleDelete(record.id)}
        />
      ),
    },
  ];

  return (
    <Card title="Danh sách giáo viên">
      <Row>
        <Col span={24}>
          <CartHeadAction>
            <Link to={routeConstant.DANH_MUC__CA_THI__NHAP_DU_LIEU}>
              <BtnAddNew />
            </Link>
          </CartHeadAction>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <TableData reload={reload} columns={columns} service={getTestSessions} />
        </Col>
      </Row>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <TestSession />
  </Page>
);



